import { Components } from '@ionic/core';
import { defineCustomElement } from '@ionic/core/components/ion-skeleton-text';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';

defineCustomElement();

const meta: Meta<Components.IonSkeletonText & any> = {
  title: 'components/Elementor/Ecommerce/Product item',
  argTypes: {
    ['tp-component']: {
      control: { type: 'select' },
      options: ['tp-elem-product-item'],
      description: 'Define o skeleton a ser renderizado.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: '' },
      },
    },
  },
  args: {
    ['tp-component']: 'tp-elem-product-item',
  },
};

export default meta;

type Story = StoryObj<Components.IonSkeletonText & any>;

export const Skeleton: Story = {
  render: ({ ...args }) => html`
    <ion-skeleton-text
      tp-component="${args['tp-component']}"
      animated
    ></ion-skeleton-text>
  `,
};
Skeleton.argTypes = {
  color: { table: { disable: true } },
};
